@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.searchResult {
  display: flex;
  position: relative;
  margin: 20px;
  padding: 20px;
  border-bottom: 1px solid lightgray;
  cursor: pointer;
}
.searchResult:hover {
  opacity: 0.8;
}
.searchResult > img {
  height: 200px;
  width: 350px;
  border-radius: 10px;
  overflow: hidden;
}
.searchResult__info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 20px;
}
.searchResult__heart {
  position: absolute;
  top: 20px;
  right: 40px;
}
.searchResult__infoTop {
  width: 40vw;
}
.searchResult__infoTop > h3 {
  font-weight: 300;
  margin-top: 10px;
}
.searchResult__infoTop > p {
  margin-top: 10px;
  font-size: 13px;
  color: gray;
}
.searchResult__infoBottom {
  display: flex;
  justify-content: space-between;
}
.searchResult__heart {
  position: absolute;
  top: 20px;
  right: 40px;
}
.searchResult__infoTop {
  width: 40vw;
}
.searchResult__infoTop > h3 {
  font-weight: 300;
  margin-top: 10px;
}
.searchResult__infoTop > p {
  margin-top: 10px;
  font-size: 13px;
  color: gray;
}
.searchResult__star {
  color: red;
}
.searchResult__stars {
  display: flex;
  align-items: center;
}
.searchResult__price > p {
  text-align: right;
}
.searchResult__price {
  position: absolute;
  bottom: 20px;
  right: 30px;
}

.banner {
  background: url(/static/media/banner.5fef73f9.jpg) center center;
  height: 71vh;
  background-repeat: no-repeat;
  position: relative;
  width: 100%;
}
.banner__info {
  background-color: rgb(61, 47, 47);
  color: white;
  padding-top: 25vh;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 40px;
  width: 300px;
  opacity: 0.8;
}
.banner__search {
  display: flex;
  flex-direction: column;
}
.banner__searchButton {
  background-color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  color: #dac612 !important;
}
.banner__info > button {
  background-color: #d1282b;
  color: white;
  text-transform: inherit;
  margin-top: 20px;
  font-weight: 600;
}
.banner__info > h5 {
  margin-top: 10px;
}
.banner__info > button:hover {
  background-color: white;
  color: tomato;
}

.datepicker {
  position: absolute;
  top: 35px;
  left: 26%;
  width: 90vw;
  z-index: 50;
}
.datepicker > h2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 559px;
  padding: 10px;
  background: white;
  position: absolute;
  left: 0;
  top: 327px;
}

.datepicker > input {
  width: 539px;
  padding: 20px;
  position: absolute;
  left: 0;
  height: 30px;
  top: 398px;
  border: none;
}
.datepicker > input:focus {
  outline-width: 0;
}
.datepicker > button {
  position: absolute;
  left: 0;
  top: 466px;
  text-transform: inherit !important;
  background-color: #0fc991;
  width: 579px;
  color: white;
}
.datepicker > button:hover {
  background-color: white;
  color: #0fc991;
}

.home__section {
  display: flex;
  padding: 30px;
}
.card {
  margin: 10px;
}

.card {
  margin: 10px;
  border: 0px solid black;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 6px 18px -9px rgba(0, 0, 0, 0.75);
  transition: transform 100ms ease-in;
  cursor: pointer;
}
.card > img {
  object-fit: fill;
  min-width: 300px;
  min-width: 200px;
  width: 100%;
}
.card:hover {
  transform: scale(1.07);
}

.card__info {
  margin-top: -9px;
  border-radius: 10px;
  padding: 20px;
  padding-top: 20px;
  border: 1;
}
.card__info > h2 {
  font-size: 18px;
  font-weight: 600;
}
.card__info > h4 {
  font-size: 14px;
  font-weight: 300;
  margin-top: 8px;
  margin-bottom: 8px;
}

.footer {
  border: 1px solid lightgrey;
  padding: 20px;
  background-color: #f7f7f7;
  text-align: center;
}
.footer > p {
  padding: 5px;
  font-size: 14px;
}

.header__icon {
  object-fit: contain;
  height: 100px;
  margin-left: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  background-color: white;
  z-index: 100;
  width: 100%;
}
.header__center {
  display: flex;
  flex: 1 1;
  align-items: center;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding: 10px;
  height: 30px;
  border: 1px solid lightgrey;
  border-radius: 999px;
}
.header__center > input {
  border: none;
  outline-width: 0;
  padding: 10px;
  /* width: 250px; */
}

.header__right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 15vw;
  margin-right: 80px;
}

.admin__login {
  cursor: pointer;
  text-decoration: none;
  border-radius: 20px;
  color: black;
}
.admin__login:hover {
  background-color: rgb(241, 240, 240);
}

.base-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.base-container .header {
  font-size: 24px;
  font-family: "Open Sans", sans-serif;
}

.base-container .content {
  display: flex;
  flex-direction: column;
}

.base-container .content .image {
  width: 21em;
}

.base-container .content .image img {
  width: 50%;
  height: 50%;
}

.base-container .content .form {
  margin-top: 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.base-container .content .form .form-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.base-container .content .form .form-group label {
  font-size: 20px;
}

.base-container .content .form .form-group input {
  margin-top: 6px;
  min-width: 18em;
  height: 37px;
  padding: 0px 10px;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  background-color: #f3f3f3;
  border: 0;
  border-radius: 4px;
  margin-bottom: 31px;
  transition: all 250ms ease-in-out;
}

.base-container .content .form .form-group input:focus {
  outline: none;
  box-shadow: 0px 0px 12px 0.8px #0fc991;
}

.base-container .footer {
  margin-top: 1em;
}

.Admin {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Open Sans", sans-serif;
}

.Admin-logo {
  animation: Admin-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.Admin-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Admin-link {
  color: #61dafb;
}

@keyframes Admin-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.login {
  width: 27em;
  height: 40em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  position: relative;
  z-index: 99;
}

.login .container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.2);
  border-radius: 4px;
  position: relative;
  z-index: 99;
  width: 100%;
  height: 100%;
  z-index: 99;
  padding: 17px 10px;
}

.login .right-side {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 90%;
  background-color: #0fc991;
  width: 100%;
  position: absolute;
  right: -34%;
  border-radius: 6px;
  z-index: 1;
  transition: all 400ms ease-in-out;
  cursor: pointer;
  box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.281);
}

.login .right-side.right {
  right: -40%;
  align-items: flex-end;
}

.login .right-side.right:hover {
  right: -45%;
}

.login .right-side.left {
  right: 40%;
  align-items: flex-start;
}

.login .right-side.left:hover {
  right: 45%;
}

.login .right-side .text {
  font-size: 21px;
  font-weight: 500;
  color: #fff;
  margin-right: 3em;
  margin-left: 3em;
}

.btn {
  font-size: 21px;
  padding: 5px 20px;
  border: 0;
  background-color: #0fc991;
  color: #fff;
  border-radius: 3px;
  transition: all 250ms ease-in-out;
  cursor: pointer;
}

.btn:hover {
  background-color: #0fc991;
}

.btn:focus {
  outline: none;
}

