.header__icon {
  object-fit: contain;
  height: 100px;
  margin-left: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 0;
  position: sticky;
  background-color: white;
  z-index: 100;
  width: 100%;
}
.header__center {
  display: flex;
  flex: 1;
  align-items: center;
  max-width: fit-content;
  padding: 10px;
  height: 30px;
  border: 1px solid lightgrey;
  border-radius: 999px;
}
.header__center > input {
  border: none;
  outline-width: 0;
  padding: 10px;
  /* width: 250px; */
}

.header__right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 15vw;
  margin-right: 80px;
}

.admin__login {
  cursor: pointer;
  text-decoration: none;
  border-radius: 20px;
  color: black;
}
.admin__login:hover {
  background-color: rgb(241, 240, 240);
}
